.go-to-quizz {
    display: flex;
    justify-content: center;
    padding: 20px; 
    border: 2px solid rgb(242, 236, 236);
    border-style: solid;
    background-color: rgb(47, 89, 255); 
    margin-right: 40%;
    margin-left: 40%;
    border-radius: 30px;
    transition: 1s;
    box-shadow: -10px 7px 5px navy;
    margin-bottom: 2%;
}

.go-to-quizz:hover {
    display: flex;
    justify-content: center;
    padding: 2%; 
    border: 2px solid rgb(249, 246, 246);
    border-style: solid;
    transition: 2s;
    background-color: rgb(106, 17, 239); 
    margin-right: 40%;
    margin-left: 40%;
    border-radius: 30px;
    box-shadow: -10px 7px 5px navy;
}

.link-to-quizz {
    color: white;
    text-decoration: none;
    font-size: 20px;
}

.link-to-quizz:hover {
    color: rgb(239, 234, 234);
    font-size: 25px;
    transition: 1s;
}


